.header-container {
    position: relative;
    z-index: 2;
    background-color: #333;
    color: #fff;
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: fadeIn 1s ease forwards;
}

.logo-name {
    padding-left: 20px;
}
.logo-link {
    text-decoration: none;
    color: #fff;
}

nav ul {
    list-style-type: none;
    display: flex;
}
  
nav ul li {
    margin-right: 20px;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #ff9900;
}
