.about-container {
  text-align: center;
  padding: 50px;
}

.about-header {
  margin-bottom: 50px;
  animation: fadeIn 1s ease;
}

.about-content {
  display: flex;
  justify-content: space-around;
  animation: fadeIn 1s ease;
}

.content-section {
  width: 30%;
}

.content-section h2 {
  font-size: 24px;
}

.content-section p {
  font-size: 16px;
  line-height: 1.6;
}

.content-section {
  position: relative;
}






