.features {
    display: flex;
    justify-content: space-around;
    padding: 50px 0;
    animation: fadeIn 1s ease forwards;
}
  
.feature {
    text-align: center;
    flex: 1;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.feature:hover {
    transform: translateY(-5px);
}