.contact-container {
    text-align: center;
    padding: 50px;
}
  
.contact-header {
    margin-bottom: 50px;
    animation: fadeIn 1s ease;
}

.contact-header h1 {
  font-family: '볼드', sans-serif; /* 원하는 글꼴로 변경 */
}

.contact-header p {
    font-size: 18px;
}
  
.contact-content {
    display: flex;
    justify-content: space-around;
    animation: fadeIn 1s ease;
}
  
.contact-section {
    width: 30%;
}
  
.contact-section h2 {
    font-family: sans-serif;
    font-size: 24px;
}
  
.contact-section p {
    font-family: sans-serif;
    font-size: 16px;
    line-height: 1.6;
}