.Main {
    position: relative;
    z-index: 1;
    color: #ffffff;
    text-align: center;
    padding: 100px 0;
    animation: fadeIn 1s ease forwards;
    font-size: 30px;
  }
  
.Main-content {
    position: relative;
    z-index: 3;
    max-width: 800px;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    border-radius: 10px;
}

.Main-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #ff9900;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}
  
.btn:hover {
    background-color: #ff6600;
}